<template>
  <div class="contact-me">

    <Dialog v-model="dio">
      <template>
        <h1 style="font-size: 3em; padding-right: 50px;">Message Sent</h1>
        <div style="text-align: left; font-size: 1.2em; margin-top: 20px;">I will view this as soon as i can</div>
        <div @click="dio = false" class="btn confirm">Sounds good!</div>
      </template>
    </Dialog>

    <Dialog v-model="alertDio">
      <template>
        <h1 style="font-size: 3em; padding-right: 50px;">Uh Oh!</h1>
        <div style="text-align: left; font-size: 1.2em; margin-top: 20px;">{{ alertMessage }}</div>
        <div @click="alertDio = false" class="btn confirm">Sounds good!</div>
      </template>
    </Dialog>

    <div @click="$router.push('/')" class="back-arrow">
      <unicon name="corner-up-left-alt" fill="black"></unicon>
    </div>

    <div class="floating-links">
      <div class="link">
        <div class="left">
          <unicon name="calling" :fill="'white'"></unicon>
        </div>
        <div class="right">
          801 661 1623
        </div>
      </div>
      <div class="link">
        <div class="left">
          <unicon name="envelope" :fill="'white'"></unicon>
        </div>
        <div class="right">
          caleb@calebjustice.com
        </div>
      </div>
    </div>

    <div class="top">
      <div class="header"> {{ $route.query && $route.query.port ? 'More Than Just A Portfolio' : 'Contact Me' }} </div>
      <div class="sub-header">{{ $route.query && $route.query.port ? 'This is you, YOUR website, show off a bit. Showcase work to recruiters, or even friends.' : 'Email me directly, call me, or message me through here'}}</div>
      <div>{{ $route.query && $route.query.port ? 'Getting a portfolio is easy as paying a small purchase price, picking from my pre-designed templates (or a new design), and contacting me directly.' : 'I check my messages on an almost daily basis' }}</div>
    </div>

    <div class="bottom">
      <div class="header">Get In Touch</div>
      <div>Text me, contact me directly through email, or contact me here.</div>

      <div class="form">
        <div class="text-boxes">
          <div class="input">
            <unicon name="user" :fill="'#4e4e4e'"></unicon>
            <input v-model="name" type="text" placeholder="Your Name" />
          </div>
          <div class="input">
            <unicon name="calling" :fill="'#4e4e4e'"></unicon>
            <input v-model="phone" type="text" placeholder="Your Phone Number (Optional)" />
          </div>
          <div class="input">
            <unicon name="envelope" :fill="'#4e4e4e'"></unicon>
            <input v-model="email" type="text" placeholder="Your Email" />
          </div>
        </div>

        <div class="input text-area">
          <unicon class="alt-icon" name="comment-alt" :fill="'#4e4e4e'"></unicon>
          <textarea v-model="message" placeholder="Your Message" />
        </div>
      </div>

      <div @click="sendEmail" class="btn">
        Submit
      </div>
    </div>
  </div>
</template>

<script>

import axios from 'axios';
import Dialog from '../common/Dialog.vue';

  export default {
  components: { Dialog },
    name: 'ContactMe1',
    data(){
      return {
        email: '',
        phone: '',
        name: '',
        message: '',
        dio: false,
        alertDio: false,
        alertMessage: ''
      }
    },
    methods: {
      sendEmail() {
        if (!this.email || !this.message) {
          this.alertDio = true;
          this.alertMessage = 'Must enter email and a message';
          return;
        }
        axios.defaults.headers.post['Content-Type'] = 'application/json';
        axios.post('https://calebjustice.com:3030/api/defaults/send-email', JSON.stringify({sentFromEmail: this.email, message: this.message, name: this.name, phone: this.phone})).then(() => {
          this.dio = true;
        }).catch((er) => {
          this.alertDio = true;
          this.alertMessage = 'Something went wrong, Ill look into it.' + er.toString();
        })
      }
    }
  };
</script>

