<template>
    <transition name="fade">
        <div class="notification" id="noti" @click="close" v-if="value">
            <div class="top">
                <div class="header">{{ header }}</div>
                <div class="close">x</div>
            </div>
            <div class="main">
                <slot>

                </slot>
            </div>
        </div>
    </transition>
</template>

<script>

import $ from 'jquery';

export default {
    name: 'Notification',
    props: {
        info: String,
        header: String,
        content: String,
        value: Boolean,
        timeout: {
            type: String,
            default: () => '10000'
        }
    },
    mounted() {
        $('#noti').hide();
        setTimeout(() => {
            $('#noti').slideDown(300);
        }, 1000);
        setTimeout(() => {
            if (this.value) {
                this.$emit('input', false);
            }
        }, this.timeout)
    },
    methods: {
        close() {
            this.$emit('input', false);
        }
    }
}
</script>

<style scoped>
    .notification {
        z-index: 10;
        border-left: 10px solid #20B8BA;
        background-color: white;
        position: fixed;
        top: 10px;
