        padding: 10px;
        box-shadow: 0px 0 1em -.5em #000, -.5em 1em 5em -1em rgba(0,0,0,.7);
        width: 300px;
        padding-right: 35px;
        transform: scale(0.8);
    }

    .top {
        position: relative;
        display: flex;
    }

    .close {
        position: absolute;
        font-weight: lighter;
        color: darygrey;
        cursor: pointer;
        right: -20px;
        top: -11px;
        font-size: 1.8em;
    }

    .primary-text {
        color: #20B8BA;
        font-style: italic;
        font-size: 1.3em;
        margin-right: 15px;
    }
    .header {
        font-size: 1.3em;
    }
    .main {
        margin: 12px 0 4px 5px;
    }
    
    .fade-enter-active, .fade-leave-active {
        transition: all 1s;
    }   
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
        margin-left: -700px;
    }

    @media only screen and (max-width: 900px) {
        .notification {
