import Vue from 'vue'
import App from './App.vue'
import Unicon from 'vue-unicons/dist/vue-unicons-vue2.umd';
import {uniLayerGroupMonochrome, uniCarWash, uniCornerUpRight, uniCornerLeftDown, uniGraduationCap, uniCalling, uniEnvelope, uniUser, uniCommentAlt, uniCornerUpLeftAlt} from 'vue-unicons/dist/icons';
import VueRouter from 'vue-router';
import routes from './router/routes';

Unicon.add([uniCarWash, uniLayerGroupMonochrome, uniCornerUpRight, uniCornerLeftDown, uniGraduationCap, uniCalling, uniEnvelope, uniUser, uniCommentAlt, uniCornerUpLeftAlt]);

Vue.use(Unicon);

Vue.config.productionTip = false

Vue.use(VueRouter)

const router = new VueRouter({
  routes
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
